'use client';

import { useState } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { 
  Home, 
  Users, 
  Image as ImageIcon, 
  Bookmark, 
  Compass, 
  Plus,
  Moon,
  Sun
} from 'lucide-react';
import { Avatar } from '@mantine/core';
import { useAuth } from '@/hooks/useAuth';

const LeftSidebar = () => {
  const [isDarkMode, setIsDarkMode] = useState(false);
  const pathname = usePathname();
  const { user } = useAuth();

  const menuItems = [
    { icon: Home, label: 'News Feed', href: '/' },
    { icon: Users, label: 'Friends', href: '/friends' },
    { icon: ImageIcon, label: 'Reels', href: '/reels' },
    { icon: Bookmark, label: 'Saved', href: '/saved' },
    { icon: Compass, label: 'Explore', href: '/explore' },
  ];

  return (
    <aside className="fixed left-0 top-16 h-[calc(100vh-4rem)] w-64 bg-[#FAFAFA] dark:bg-[#121212] border-r border-[#DBDBDB] dark:border-[#262626] overflow-y-auto">
      <div className="p-4">
        {/* Profile Summary */}
        <Link href="/profile" className="flex items-center space-x-2 p-1 rounded-lg hover:bg-[#EFEFEF] dark:hover:bg-[#262626]">
          <Avatar
            src={user?.avatar || undefined}
            alt={user?.username || "Profile"}
            size="lg"
            radius="xl"
          />
          <div>
            <h3 className="font-medium text-[#262626] dark:text-[#FAFAFA]">{user?.username || "Guest"}</h3>
            <p className="text-sm text-[#666666] dark:text-[#A0A0A0]">View Profile</p>
          </div>
        </Link>

        {/* Quick Menu */}
        <nav className="mt-6">
          {menuItems.map((item) => (
            <Link
              key={item.href}
              href={item.href}
              className={`flex items-center space-x-3 p-2 rounded-lg mb-1 ${
                pathname === item.href
                  ? 'bg-[#EFEFEF] dark:bg-[#262626] text-[#3897F0]'
                  : 'text-[#262626] dark:text-[#FAFAFA] hover:bg-[#EFEFEF] dark:hover:bg-[#262626]'
              }`}
            >
              <item.icon size={20} />
              <span>{item.label}</span>
            </Link>
          ))}
        </nav>

        {/* Theme Toggle */}
        <button
          onClick={() => setIsDarkMode(!isDarkMode)}
          className="flex items-center space-x-3 p-2 rounded-lg mt-4 text-[#262626] dark:text-[#FAFAFA] hover:bg-[#EFEFEF] dark:hover:bg-[#262626]"
        >
          {isDarkMode ? <Sun size={20} /> : <Moon size={20} />}
          <span>{isDarkMode ? 'Light Mode' : 'Dark Mode'}</span>
        </button>
      </div>
    </aside>
  );
};

export default LeftSidebar;